"use client";

import Link from "next/link";
import { useRouter } from "next/navigation";
import { ShoppingCart, Heart, LogOut } from "lucide-react";

export default function Navbar() {
  const router = useRouter();

  const handleLogout = () => {
    localStorage.removeItem("userToken");
    router.push("/login");
  };

  return (
    <nav className="bg-white shadow-md py-4 px-6 flex justify-between items-center sticky top-0 z-50">
      <Link href="/" className="text-2xl font-bold text-blue-600">
        ShopMart
      </Link>

      <div className="hidden md:flex space-x-6 text-gray-700 font-medium">
        <Link href="/" className="hover:text-blue-600">Home</Link>
        <Link href="/products" className="hover:text-blue-600">Products</Link>
        <Link href="/categories" className="hover:text-blue-600">Categories</Link>
        <Link href="/brands" className="hover:text-blue-600">Brands</Link>
      </div>

      <div className="flex items-center space-x-4">
        <Link href="/wishlist" className="text-gray-600 hover:text-red-500">
          <Heart size={24} />
        </Link>
        <Link href="/cart" className="text-gray-600 hover:text-blue-600 relative">
          <ShoppingCart size={24} />
        </Link>
        <button onClick={handleLogout} className="text-gray-600 hover:text-red-600">
          <LogOut size={24} />
        </button>
      </div>
    </nav>
  );
}